import Vue from "vue";

import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
	{
		path: "/",
		name: "home",
		component: () => import(/* webpackChunkName: "home" */ "../views/Home"),
	},
	{
		path: "/signup",
		name: "signup",
		component: () => import(/* webpackChunkName: "signup" */ "../views/auth/provider/Signup"),
	},
	{
		path: "/login",
		name: "login",
		component: () => import(/* webpackChunkName: "login" */ "../views/auth/provider/Login"),
	},
	{
		path: "/signup/local",
		name: "localSignup",
		component: () => import(/* webpackChunkName: "localSignup" */ "../views/auth/local/Signup"),
	},
	{
		path: "/login/local",
		name: "localLogin",
		component: () => import(/* webpackChunkName: "localLogin" */ "../views/auth/local/Login"),
	},
	{
		path: "/forget",
		name: "forgetPassword",
		component: () => import(/* webpackChunkName: "forgetPassword" */ "../views/auth/local/ForgetPassword"),
	},
	{
		path: "/events",
		name: "events",
		component: () => import(/* webpackChunkName: "events" */ "../views/Events"),
	},
	{
		path: "/events/:id",
		name: "event",
		component: () => import(/* webpackChunkName: "event" */ "../views/Event"),
	},
	{
		path: "/invites",
		name: "invites",
		component: () => import(/* webpackChunkName: "invites" */ "../views/Invites"),
	},
	{
		path: "/create",
		name: "create",
		component: () => import(/* webpackChunkName: "create" */ "../views/Create"),
	},
	{
		path: "/create/one",
		name: "createOne",
		component: () => import(/* webpackChunkName: "createOne" */ "../views/CreateOne"),
	},
	{
		path: "/create/one/when",
		name: "whenOne",
		component: () => import(/* webpackChunkName: "whenOne" */ "../views/WhenOne"),
	},
];

const router = new VueRouter({
	mode: "history",
	base: process.env.BASE_URL,
	routes,
});

export default router;
